}

.FooterContainer > article > div > aside > .Link:hover {
    text-decoration: underline;
    text-underline-offset: 8px;
    -webkit-text-decoration-color: var(--green);
            text-decoration-color: var(--green);
} 

.FooterContainer > aside {
    width: 90%;
    margin: auto;
    display: flex;
    align-items: center;
    grid-gap: 10px;
    gap: 10px;
    margin: 40px auto 0px auto;
}

.FooterContainer > aside > p {
    color: var(--white);
    font-size: 14px;
}

.FooterContainer > aside > div {
    height: 40px;
    width: 1px;
    background-color: var(--white);
}

.FooterContainer > aside > img {
