}

@media only screen and (max-width: 650px) {

    .FooterContainer > .ContainerForLogoAndLink > img {
        width: 225px;
    }

    .FooterContainer > .Line {
        display: none;
    }

    .FooterContainer > article {
        width: 98%;
        margin: auto;
        display: flex;
        flex-direction: column;
        grid-gap: 30px;
        gap: 30px;
    }

    .FooterContainer > article > div {
        width: 95%;
        border-bottom: 1px solid var(--white);
        grid-gap: 0;
        gap: 0;
        overflow: hidden;
    }

    .FooterContainer > article > div > div {
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: space-between;
        padding: 10px 20px;
        width: 100%;
        cursor: pointer;
    }

    .FooterContainer > article > div > h2 {
        font-size: 22px;
    }

    .FooterContainer > article > div > div > .OpenCloseFooterLinks {
        display: block;
    }
    
    .FooterContainer > article > div > aside {
        flex-direction: row;
        width: 90%;
        margin: auto;
        visibility: hidden;
        pointer-events: none;
        height: 0;
        padding: 0;
        opacity: 0;
        transition: .5s all;
    }

    .FooterContainer > article > div > aside.open {
        opacity: 1;
        visibility: visible;
        pointer-events: all;
        height: auto;
        padding: 20px 0px;
    }

}

@media only screen and (max-width: 470px) {

    .FooterContainer > article > div > aside {
        flex-direction: column;
    }

    .FooterContainer > aside {
        width: 95%;
        grid-gap: 5px;
        gap: 5px;
        margin: 20px auto 0px auto;
        justify-content: center;
    }
    
    .FooterContainer > aside > p {
        font-size: 12px;
        margin: 0;
    }
    
    .FooterContainer > aside > div {
        height: 30px;
    }
    
    .FooterContainer > aside > img {
        width: 30px;
    }

    .FooterContainer > .ContainerForLogoAndLink {
        flex-direction: column;
        grid-gap: 10px;
        gap: 10px;
    }
    
    .FooterContainer > .ContainerForLogoAndLink > img {
        width: 250px;
    }
    
}

@keyframes fadeInMoveDown {
    from {
        opacity: 0;
        top: 25px;
    }
    to {
        opacity: 1;
        top: 0px;
    }
}


/* hero */
