.errorMessageBackground {
    visibility: hidden;
    pointer-events: none;
    opacity: 0;
    transition: .5s all;
}

.errorMessageBackground.show {
    position: fixed;
    visibility: visible;
    pointer-events: all;
    opacity: 1;
    top: 0;
    left: 0;
    background-color: rgba(0, 0, 0, 0.717);
    width: 100%;
    height: 100%;
}

.errorMessageContainer {
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    visibility: hidden;
    pointer-events: none;
    opacity: 0;
    transition: 1s all;
}

.errorMessageContainer.show {
    visibility: visible;
    pointer-events: all;
