    align-items: center;
    justify-content: center;
    margin: 100px auto 50px auto;
    width: 90%;
}

.workouts > article > h1 {
    inline-size: 75%;
    font-family: var(--primary-font);
    font-size: 42px;
    margin: 0;
}

.workouts > article > ul {
    padding: 0;
    margin: 0;
    display: flex;
    flex-wrap: wrap;
    list-style-type: none;
    grid-column-gap: 20px;
    column-gap: 20px;
    grid-row-gap: 30px;
    row-gap: 30px;
}

.workouts > article > ul > li > .linkToWorkouts {
    background-color: var(--white);
    border-radius: 15px;
    color: var(--black);
    border: 1px solid var(--black);
    text-decoration: none;
    padding: 10px;
    transition: .5s all;
}

.workouts > article > ul > li > a:hover {
    background-color: var(--green);
    color: var(--white);
    border: 1px solid var(--white);
}

.workouts > main {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
    max-width: 1400px;
    inline-size: 95%;
    margin: 50px auto 50px auto;
    grid-gap: 30px;
    gap: 30px;
}

.workouts > main > .linkToWorkout {
    text-decoration: none;
    color: var(--black);
    border: .5px solid var(--black);
    border-radius: 15px;
    padding: 10px;
    display: flex;
    flex-direction: column;
    align-items: center;
    grid-gap: 10px;
    gap: 10px;
    width: 400px;
}

.workouts > main > a > img {
    width: 100%;
}

.workouts > main > a > h2 {
    letter-spacing: 2px;
    font-size: 18px;
    text-decoration: underline;
}

@media screen and (max-width: 950px) {

